import styled from "styled-components";
import { EngLoc } from "../../assets/constants/localizations";
import { Text, Title3 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonMobile";

const SService = styled.div`
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  gap: 30px;

  max-width: 400px;

  & button {
    width: 50%;
  }
`;

const Title = styled(Title3)`
  font-weight: 300;
  font-size: 35px;
  line-height: 130%;

  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
`;

const TitleImg = styled.img`
  height: 70px;
  width: 70px;
`;

const Service = ({ title, desc, img, language }) => {
  return (
    <SService>
      <Title>
        <TitleImg loading="lazy" src={img} /> {title}
      </Title>
      <Text size="18" color="#3C3C3C;">
        {desc}
      </Text>
      <Button type="service" colorTxt="#1073D3" color="#E7F5FF" link={true}>
        {language ==="Ru" ? "Подробнее" : EngLoc.buttons.more}
      </Button>
    </SService>
  );
};

export default Service;
