import styled from "styled-components";

import work from "../../assets/images/Work.svg";
import { Title3 } from "../../assets/styles/component";
import Service from "./serviceMobile";

import Target from "../../assets/images/Target.svg";
import Person from "../../assets/images/person.svg";
import { EngLoc } from "../../assets/constants/localizations";

const SWork = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 60px;
  margin: 0 15px;
  padding: 60px 0;
`;

const TitleBox = styled.div`
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;

  max-width: 700px;

  text-align: center;
`;

const WorkImg = styled.img`
  width: 50%;
`;

const Title = styled(Title3)`
  font-size: 48px;
  font-weight: 400;
`;

const Services = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  gap: 60px;
`;

const Line = styled.div`
  height: 1px;
  width: 50%;
  max-width: 700px;
  margin: auto;
  background: #94bcf5;
`;

const Work = ({ language }) => {
  return (
    <SWork id={"services"}>
      <TitleBox>
        <Title weight={300}>
          {language === "Ru" ? "Как мы работаем" : EngLoc.services.title}
        </Title>
        <WorkImg loading="lazy" src={work} />
      </TitleBox>
      <Services>
        <Service
        language={language}
          title={
            language === "Ru"
              ? "Full-сервис"
              : EngLoc.services.services_list[0].title
          }
          desc={
            language === "Ru"
              ? "Платформа для получения лидов со всех источников с оплатой за действия. Все включено и еще текст для примера"
              : EngLoc.services.services_list[0].desc
          }
          img={Target}
        />
        <Line />
        <Service
        language={language}
        title={
            language === "Ru"
              ? "Self-сервис"
              : EngLoc.services.services_list[1].title
          }
          desc={
            language === "Ru"
              ? "Платформа для получения лидов со всех источников с оплатой за действия. Все включено и еще текст для примера"
              : EngLoc.services.services_list[1].desc
          }
          img={Person}
        />
      </Services>
    </SWork>
  );
};

export default Work;
